body {
  background-color: #222 !important;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*file with hover and media styles*/
.PopoverButton:hover {
  background-color: #222 !important;
  color: #28a745 !important;
}

.btnEnglish:hover {
  background-color: #222 !important;
  color: #28a745;
}

.btnPolish:hover {
  background-color: #222 !important;
  color: #28a745;
}

.btnExit:hover {
  color: white;
  cursor: pointer;
}

.desktopHide {
  display: none !important;
}

.mobileMenu {
  position: fixed;
  background-color: #28a745;
  padding: 2em;
  height: 100%;
  margin-top: -5%;
  width: 70%;
  left: 0;
  top: 0;
}

@media screen and (max-width: 800px) {
  .search-main {
    margin-top: 20% !important;
  }
}

@media screen and (max-width: 500px) {
  footer img {
    width: 8% !important;
  }

  footer {
    font-size: 1em;
  }

  .search-main {
    margin-top: 20% !important;
    width: 80% !important;
    margin-left: 0% !important;
    display: block;
    float: left;
  }

  .mobileHide {
    display: none !important;
  }

  .mobileShow {
    display: block !important;
  }

  .imgExit {
    margin-top: 20% !important;
    float: left;
    display: block;
    width: 12%;
  }
  .mobileMenuImage {
    float: right;
    padding: 0 !important;
    margin-top: -14% !important;
    width: 10%;
  }
  .mobileMenu img {
    float: right;
    top: 0;
    margin-right: -10%;
    width: 10%;
  }
  .btnEnglish,
  .btnPolish {
    width: 36%;
    display: block;
    margin: 5%;
  }
}

